<template>
  <section class="main">
    <section class="left">
      <section class="header">
        <a-menu v-model="current" mode="horizontal" >
          <a-menu-item key="music">
            <router-link to="/myDays/music">
              <a-icon type="customer-service" />音乐
            </router-link>
          </a-menu-item>
          <a-sub-menu>
            <span slot="title" class="submenu-title-wrapper">
              <a-icon type="play-circle" />电影
            </span>

            <a-menu-item key="movie">
              <router-link :to="{name:'movie', params:{type:'recommond'}}">
                <a-icon type="customer-service" />重磅推荐
              </router-link>
            </a-menu-item>
            <a-menu-item key="movies">
              <router-link :to="{name:'movie', params:{type:'recent'}}">
                <a-icon type="customer-service" />最近在看
              </router-link>
            </a-menu-item>
          </a-sub-menu>
          <a-menu-item key="book">
            <router-link to="/myDays/book">
              <a-icon type="read" />图书
            </router-link>
          </a-menu-item>
        </a-menu>
      </section>
      <section class="content">
        <router-view></router-view>
      </section>
    </section>

    <section class="right"></section>
  </section>
</template>
<style lang="scss" scoped>
.main {
  display: flex;

  .left {
    width: 60%;
    .header {
      width: 100vh;
    }
    .content {
        padding-top: 30px
    }
  }
  .right {
    width: 40%;
  }
}
</style>
<script>
export default {
  data() {
    return {
      mode: "inline",
      theme: "dark",
      current: ["movie"]
    };
  },
  methods: {
    changeTheme(checked) {
      this.theme = checked ? "dark" : "light";
    }
  }
};
</script>


